@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper__post {
  width: 800px;
  min-height: 400px;
  background-color: rgb(48, 163, 163);
  border: 5px solid black;
  margin: 20px auto;
  font-size: 18px;
  font-weight: 500;
  color: rgb(27, 22, 22);
}

.author {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  min-height: 25%;
}

.author-avatar {
  padding-left: 15px;
  padding-top: 5px;
  width: 15%;
}

.author-avatar img {
  width: 75px;
  height: 75px;
  border-radius: 50%;
}

.author-info {
  width: 85%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding-top: 5px;
}

.author-info div {
  margin-left: 5px;
  margin-right: 10px;
}

.author-info .author-name {
  font-weight: bold;
  margin-right: 0;
}

.author-info .author-nickname,
.author-info .date {
  color: rgb(54, 54, 54);
}

.author-info .info-option {
  margin-left: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.author-icon img,
.info-option img,
.social img {
  width: 20px;
  height: 20px;
}

.content .content-text {
  text-align: left;
  width: 85%;
  margin-left: 15%;
  margin-top: -5%;
  padding-left: 5px;
}

.content-image {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding-top: 0;
  margin-right: 10px;
}

.content-image img {
  margin-top: 5px;
  width: 85%;
  height: auto;
  border-radius: 3%;
}

.social {
  display: -webkit-flex;
  display: flex;
  padding-left: 15%;
  margin-top: 10px;
}

.social div {
  margin-right: 15%;
  font-size: 16px;
  padding-bottom: 10px;
  color: rgb(27, 22, 22);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.addContentForm {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 10px auto;
  padding: 20px;
  width: 400px;
  min-height: 400px;
  font-family: 'Roboto', sans-serif;
  background-color: #000;
  border: 1px solid grey;
  color: grey;
}

.addContentForm select {
  width: 50%;
  color: grey;
  background-color: #000;
}

.addContentForm input {
  display: -webkit-flex;
  display: flex;
  width: 85%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: grey;
  background-color: #000;
  border: 1px solid grey;
  padding: 10px;
  margin: 20px;
}

.addContentForm textarea {
  display: -webkit-flex;
  display: flex;
  width: 85%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: grey;
  background-color: #000;
  border: 1px solid grey;
  padding: 10px;
  margin: 20px;
}

.addContentForm__submit {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 90%;
  margin: 10px auto;
  padding: 10px;
  background-color: aquamarine;
}

